<template>
    <div class="outPutData">
        
    </div>
</template>

<script>
import backData from '@/assets/data.js'
import XLSX from 'xlsx'
const path=require('path');
export default {
    data() {
        return {
            baseInfo: null,
            algorithm: null,
            inputList: []
        };
    },
    computed: {},
    mounted() {
        // this.groupList = backData.groupList
        // this.dealAlgorithm()
    },
    methods: {
        cloneJson(obj) {
            return JSON.parse(JSON.stringify(obj));
        },
        // 生成excel
        outputXlsxFile (ws, xlsxName) {
            const wb = XLSX.utils.book_new()
            XLSX.utils.book_append_sheet(wb, ws, xlsxName)
            XLSX.writeFile(wb,  this.baseInfo.orderNum + '-' + xlsxName + '.xlsx')
        },  
        initData(baseInfo, algorithm, inputList) {
            this.baseInfo = baseInfo
            this.algorithm = algorithm
            this.inputList = inputList
            this.dealAlgorithm()
        },
        // 做算法的预处理
        dealAlgorithm() {
            let baseInfo = this.baseInfo
            let algorithm = this.algorithm
            // 套线
            let line = algorithm.line
            let lineType = baseInfo.lineType
            let lineWidth = parseInt(lineType.substr(1, lineType.length))
            // 竖套线厚度
            let verLineTick = baseInfo.verLineTick
            // 横套线厚度
            let acrossLineTick = baseInfo.acrossLineTick
            let isEType = lineType.indexOf('E') === 0
            for(let i in line) {
                for(let j in line[i]) {
                    let item = line[i][j]
                    // 设置宽度
                    item.width = lineWidth
                    // 如果是E套线横套线或者竖套线的厚度
                    if(isEType) {
                        item.thickness = (j === 'horizontal') ? acrossLineTick : verLineTick
                        item.thickness = '厚度' + item.thickness
                    }
                    
                }
            }
            // 生成订货单
            this.outPutOrder()
            // 生成套线部分的数据
            this.outPutLineOrder()
            // 输出其他部件的位置
            this.outPutOtherPartsOrder()
            // 输出基础部件
            this.outPutbasePartsData()
        },
        // 获取高*宽*厚
        getHWTInfo(h, w, t, n) {
            let info = h + '*' + w + '*' + t
            if(n) {
                info += '=' + n
            }
            return info
        },
        // 输出订货单
        outPutOrder() {
            let baseInfo = this.baseInfo
            // 头部信息
            let firstData = [['订单编号 \n ' + baseInfo.orderNum, '', '','经销商', '', '', '', '','品牌以及包装','带商标包装'],
                                ['', '', '', '客户地址', baseInfo.orderNum.address, '', '', '', '尺寸单位:mm(毫米)', '']]
            // 名称列表
            let nameList = ['序号', '位置及名称', '门型', '材质', '颜色', '套线型号', '门洞尺寸(高*宽*墙厚)', '门扇尺寸(高*宽*墙厚)', '数量', '备注']
            let data = [nameList]
            this.inputList.forEach((ele, index) => {
                // 门洞尺寸(高*宽*墙厚)
                let doorInfo = this.getHWTInfo(ele.doorHeight, ele.doorWidth, ele.doorThick)
                // 门扇尺寸(高*宽*墙厚)
                let doorLeafInfo = this.getHWTInfo(ele.doorLeafHeight, ele.doorLeafWidth, ele.doorLeafThick)
                data.push([index + 1, ele.name, '', baseInfo.material, baseInfo.color, baseInfo.lineType, doorInfo, doorLeafInfo, '1套', ''])
            })
            const ws = XLSX.utils.aoa_to_sheet(firstData)
            ws['!cols'] = this.setColWidth([10, 20, 10, 10, 10, 10, 20, 30, 30])
            ws['!rows'] = this.setRowHeight(data)
            ws['!merges'] = [
                // 合并订单号的
                {s: {r:0,c:0},
                e: {r:1,c:2},},
                // 合并地址和经销商的
                {s: {r:0,c:4},
                e: {r:0,c:7},},
                {s: {r:1,c:4},
                e: {r:1,c:7},},
                // 合并尺寸单位的
                {s: {r:1,c:8},
                e: {r:1,c:9},},
            ]
            XLSX.utils.sheet_add_aoa(ws, data, {origin: "A3"});
            this.outputXlsxFile(ws , '订货单')
        },
        // 处理部件的数据
        outPutbasePartsData() {
            let nameMap = {
                '房门连套': 'door',
                '柜门连套': 'cupboardDoors',
                '对开门连套': 'openDoors',
                '三面窗套':'windowCover',
                '三面哑口套': 'dumb'
            }
            let conditionMap = {
                '房门连套': true,
                '柜门连套': true,
                '对开门连套': true
            }
            let nameList = ['序号', '生产单号', '颜色', '材料', '部件', '长*宽*厚=数量', '备注']
            let data = [nameList]
            let merges = []
            // 哑口套和窗套
            let otherList = []
            // 哑口套和窗套22mm板子的数量
            let otherNum = 0
            this.inputList.forEach((item, index) => {
                let name = item.name
                // 通过name找到应该使用的算法
                let algorithmList = this.algorithm[nameMap[name]]
                let totalNum = 0
                let len = algorithmList.length
                let arr = []
                algorithmList.forEach((ele, eleIndex) => {
                    let thickInfo ='多层板' + ele.thick + 'mm'
                    // 是否是连套
                    let conditionFlag = (conditionMap[item.name])
                    let l = item[ele.lenPara] + ele.length
                    let w = item.doorThick + ele.thick
                    let baseContent = [index + 1, this.baseInfo.orderNum + '-' + (index + 1), this.baseInfo.color,thickInfo]
                    // 如果是带有条件的
                    if(conditionMap[item.name]) {
                        // 档条是通过条件计算的
                        if(ele.name.indexOf('档条')  > -1 ) {
                            w = item.doorThick - ele.width.fixedLen
                            // 超过最大，就等于最大
                            if(w > ele.width.maxLen) w = ele.width.maxLen
                            // 小于最小，就等于最小
                            if(w < ele.width.minLen) w = ele.width.minLen
                        }
                        // 只计入22mm的厚度板子数量
                        if(ele.thick === 22) {
                            totalNum += ele.num
                        }
                        arr.push(baseContent.concat([ele.name, this.getHWTInfo(l, w, ele.thick, ele.num),'']))
                    }else {
                        let info = this.getHWTInfo(l, w, ele.thick, ele.num)
                        // 只计入22mm的厚度板子数量
                        if(ele.thick === 22) {
                            otherNum += ele.num
                        }
                        let partName = item.name.indexOf('窗套') > -1 ? '窗套' : '哑口'
                        // 哑口和窗套放到最后
                        otherList.push(baseContent.concat([partName, info, '']))
                    }
                })
                // 有数据的话
                if(arr.length > 0) {
                    data = data.concat(arr)
                    data.push(['门框  多层板22mm小计', '', '', '', totalNum, '', ''])
                    // 合并小计部分
                    merges.push(
                        {s: {r:data.length - 1,c:0},
                        e: {r:data.length - 1,c:3},})
                    merges.push(
                        {s: {r:data.length - 1,c:4},
                        e: {r:data.length - 1,c:5}})
                }
            })
            // 合并窗套和哑口部分
            if(otherList.length > 0) {
                data = data.concat(otherList)
                data.push(['哑口 多层板22mm小计', '', '', '', otherNum, '', ''])
                        // 合并小计部分
                merges.push(
                    {s: {r:data.length - 1,c:0},
                    e: {r:data.length - 1,c:3},})
                merges.push(
                    {s: {r:data.length - 1,c:4},
                    e: {r:data.length - 1,c:5}})
            }
            const ws = XLSX.utils.aoa_to_sheet(data)
            ws['!cols'] = this.setColWidth([10, 10, 10, 10, 10, 20, 10])
            ws['!rows'] = this.setRowHeight(data)            
            ws['!merges'] = merges
            this.outputXlsxFile(ws , '部件汇总(门框部分)')
        },
        // 处理其他部件
        outPutOtherPartsOrder() {
            let obj = {}
            let baseInfo = this.baseInfo
            let doorPart = this.algorithm.other
            let algorithmList = (baseInfo.materialType === '混油') ? doorPart.soil : doorPart.skin
            this.inputList.forEach((item, index) => {
                // 只有在房门连套的时候才会输出
                if(item.name === '房门连套') {
                    algorithmList.forEach(ele => {
                        let typeName = ele.thickType + ele.thick + 'mm'
                        // 根据板材种类分类
                        if(!obj[typeName]) obj[typeName] = []
                        let l =  item[ele.lenPara] + ele.length
                        // 如果是固定宽度
                        let w = (ele.fixedFlag) ? ele.width : item.doorLeafWidth + ele.width
                        // 材质的信息
                        ele.thickInfo = typeName
                        // 长宽厚的=数量
                        ele.info = this.getHWTInfo(l, w, ele.thick, ele.num)
                        ele.index = index
                        obj[typeName].push(ele)
                    })
                }
            })
            let nameList = ['序号', '生产单号', '颜色', '材料','部件', '长*宽*厚=数量', '备注']
            let data = [nameList]
            let merges = []
            for(let i in obj) {
                let item = obj[i]
                let len = item.length
                let totalNum = 0
                item.forEach((ele, eleIndex) => {
                    let index = ele.index + 1
                    data.push([index, this.baseInfo.orderNum + '-' + index, this.baseInfo.color, ele.thickInfo, ele.name, ele.info, ''])
                    totalNum += ele.num
                    // 如果是最后一个的话
                    if(eleIndex === len - 1) {
                        data.push([ele.thickInfo, '', '', '',totalNum, '', ''])
                        // 合并小计部分
                        merges.push(
                            {s: {r:data.length - 1,c:0},
                            e: {r:data.length - 1,c:3},})
                        merges.push(
                            {s: {r:data.length - 1,c:4},
                            e: {r:data.length - 1,c:5}})
                    }
                })
            }
            const ws = XLSX.utils.aoa_to_sheet(data)
            ws['!cols'] = this.setColWidth([10, 10, 10, 10, 10, 20, 10])
            ws['!rows'] = this.setRowHeight(data)            
            ws['!merges'] = merges
            this.outputXlsxFile(ws , '部件汇总')
        },
        // 输出套线的订单
        outPutLineOrder() {
            let baseInfo = this.baseInfo
            let line = this.algorithm.line
            let nameList = ['序号', '生产单号', '颜色', '套线型号','部件', '长*宽*厚=数量', '备注']
            let data = [nameList]
            let totalNum = 0
            this.inputList.forEach((ele, index) => {
                let i = index + 1
                let name = (ele.name === '三面窗套') ? 'three' : 'normal'
                let baseArr = [i, baseInfo.orderNum + '-' + i, baseInfo.color, baseInfo.lineType]
                let arr = []
                line[name].forEach(item => {
                    let hFlag = item.name.indexOf('横') > -1
                    let name = item.name
                    let w = item.width
                    let t = item.thick
                    let n = item.num
                    let thickness = item.thickness
                    totalNum += n
                    // 如果是横套线或者是竖套线并且没有外到顶或者是  竖套线有外到顶是三面窗套
                    if(hFlag || (!hFlag && (!ele.outToTop || name === 'three'))) {
                        let l = ele[item.lenPara] + item.length
                        // 竖套线并且是三面窗套的
                        if(!hFlag && name === 'three') {
                            l = ele.outToTop + 20
                        }
                        let info = this.getHWTInfo(l, w, t, n)
                        arr.push(baseArr.concat([name, info, thickness]))
                    }else {
                        // 其中两条按照外到顶高度+20，另外两条按照内到顶高度+20
                        let outInfo = this.getHWTInfo(ele.outToTop + 20, w, t, n / 2)
                        let innerInfo = this.getHWTInfo(ele.innerToTop + 20, w, t, n / 2)
                        arr.push(baseArr.concat([name, outInfo, thickness]))
                        arr.push(baseArr.concat([name, innerInfo, thickness]))
                    }
                })
                data = data.concat(arr)
            })
            let len = data.length
            data.push(['套线' + baseInfo.lineType + '小计', '',  '', '', totalNum, '', ''])
            const ws = XLSX.utils.aoa_to_sheet(data)
            ws['!cols'] = this.setColWidth([10, 10, 10, 10, 10, 20, 10])
            ws['!rows'] = this.setRowHeight(data)
            ws['!merges'] = [
                // 合并小计部分
                {s: {r:len,c:0},
                e: {r:len,c:3},},
                {s: {r:len,c:4},
                e: {r:len,c:5},},
            ]
            this.outputXlsxFile(ws , '部件汇总(套线部分)')
        },
        // 设置行宽度
        setColWidth(list) {
            let arr = []
            list.forEach(ele => {
                arr.push({
                    wch: ele
                })
            })
            return arr
        },
        // 设置行高
        setRowHeight(data) {
            let arr = []
            data.forEach(ele => {
                arr.push({hpt: 20})
            })
            return arr
        },
        /*********************************************end******************************************* */
    }
};
</script>

<style lang="scss">
.outPutData {
    
}
</style>
